<template>
  <div class="d-flex flex-column px-sm-8" style="gap: 1rem">
    <div
      class="d-flex flex-column flex-sm-row justify-center align-center"
      style="gap: 5rem"
      :style="`min-height: ${cardHeight}`"
    >
      <div
        class="d-flex flex-column w-100 w-sm-50 align-center align-sm-start text-center text-sm-left"
        style="gap: 2rem; max-width: 840px; min-width: 540px"
      >
        <div class="d-flex w-100">
          <span
            class="text-h4 text-sm-h3 font-weight-bold mt-0 mt-sm-16 ml-16 ml-sm-0 w-75 text-center"
            style="min-height: 50px"
            :style="`color: ${twColors.palette.blue};`"
            >{{ currentText }}</span
          >
          <span
            class="text-h4 text-sm-h3 font-weight-bold mt-0 mt-sm-16 mr-16 mr-sm-0 w-25"
            style="min-height: 50px"
            >
          </span>
        </div>
        <p class="text-h6 text-sm-h3 font-weight-bold mt-0">
          
          <span
            class="text-h6 text-sm-h3 font-weight-bold mt-0 mt-sm-16"
            :style="`color: ${twColors.palette.blue};`"
            > </span
          >
            
        </p>
        <span class="text-subtitle-2 text-sm-h6">
              , .</span
        >
        <v-form class="d-flex align-center w-75 px-8 mt-8" style="gap: 1rem">
          <v-text-field
            v-if="!loading"
            class="w-50"
            style="width: 12rem"
            label=" "
            placeholder=" "
            single-line
            hide-details
            clearable
            color="blue"
            v-model="email"
          ></v-text-field>
          <v-btn
            :loading="loading"
            @click="handleSubmit"
            :style="`width: ${loading ? '100%' : '25%'}`"
            style="height: 3rem"
            variant="flat"
            :color="twColors.palette.blue"
          >
            <span
              class="text-subtitle-1 font-weight-bold"
              :style="`color: ${twColors.palette.white};`"
            >
              
            </span>
          </v-btn>
        </v-form>
        <v-dialog class="w-25" v-model="dialog">
          <v-card class="align-center py-4" style="height: 20rem">
            <v-card-text class="d-flex flex-column justify-center align-center">
              <v-icon size="7rem" color="green">mdi-check-circle-outline</v-icon>
              <p class="text-subtitle-1 text-center mt-2">  .</p>
            </v-card-text>
            <v-card-actions>
              <v-btn
                style="min-width: 5rem"
                variant="flat"
                :color="twColors.palette.blue"
                @click="dialog = false"
                ><span
                  class="text-subtitle-1 font-weight-bold"
                  :style="`color: ${twColors.palette.white};`"
                >
                  
                </span></v-btn
              >
            </v-card-actions>
          </v-card>
        </v-dialog>
      </div>
      <div class="d-flex w-50 video-style">
        <iframe class="w-100" :src="embedUrl" style="width: 20rem" frameborder="0"></iframe>
      </div>
    </div>
  </div>
</template>

<script></script>

<script setup>
import axios from "axios";

const videoUrl = ref("https://www.youtube.com/watch?v=2YLs33C72zo"); //  URL

const getVideoId = (url) => {
  const videoId = url.split("v=")[1];
  return videoId;
};

const embedUrl = computed(() => {
  const videoId = getVideoId(videoUrl.value);
  return `https://www.youtube.com/embed/${videoId}`;
});

const words = [" ", " ", "  "];
const typingSpeed = 200;
const eraseSpeed = 100;

const currentText = ref("");
let currentWordIndex = 0;
let isTyping = true;

const typeNextLetter = () => {
  if (isTyping) {
    currentText.value += words[currentWordIndex][currentText.value.length];
    if (currentText.value.length < words[currentWordIndex].length) {
      setTimeout(typeNextLetter, typingSpeed);
    } else {
      isTyping = false;
      setTimeout(eraseText, typingSpeed * 2);
    }
  } else {
    if (currentText.value.length > 0) {
      currentText.value = currentText.value.slice(0, -1);
      setTimeout(eraseText, eraseSpeed);
    }
  }
};

const eraseText = () => {
  if (currentText.value.length > 0) {
    currentText.value = currentText.value.slice(0, -1);
    setTimeout(eraseText, eraseSpeed);
  } else {
    isTyping = true;
    currentWordIndex++;
    if (currentWordIndex === words.length) {
      currentWordIndex = 0;
    }
    setTimeout(typeNextLetter, typingSpeed);
  }
};

const pageHeight = ref("");
const cardHeight = ref("");
const responseCardHeight = ref("");
const email = ref("");
const loading = ref(false);
const dialog = ref(false);
const formSparkUrl = ref("https://submit-form.com/UxEzXm6O");

const handleSubmit = async () => {
  loading.value = true;
  await axios.post(formSparkUrl.value, { email: email.value }).then((response) => {
    setTimeout(() => {
      loading.value = false;
      dialog.value = true;
      email.value = "";
    }, 500);
  });
};

onMounted(() => {
  typeNextLetter();
  const viewportHeight = window.innerHeight;
  const desiredHeight = Math.round(viewportHeight * 0.85);
  pageHeight.value = `${desiredHeight}px`;
  cardHeight.value = `${desiredHeight * 0.95}px`;
  responseCardHeight.value = `${desiredHeight * 0.8}px`;
});
</script>

